import { countryData } from 'ais-common-utils'
export default {
  props: {
    country: {
      type: Number,
      required: true
    },
    type: {
      type: String,
      default: 'code'
    }
  },
  data () {
    return {
      countryData,
      columns:countryData.map(item => (`${item.name} +${item.code}` )),
      show:false
    }
  },
  methods:{
    onConfirm(value){
      this.$emit('update:country', +value.split('+')[1])
      this.show = false
    }
  }
}
