/**导师招募接口 */
import request from "@/utils/request"
import qs from "qs"

//入驻信息提交
export const applySettled = (params) => {
  return request.post(`/school/user/teacher/recruit/apply`, qs.stringify(params))
}

//入驻信息获取
export const getSettled = () => {
  return request.post(`/school/user/teacher/recruit/index`)
}

//导师师入住简历上传
export const settledUpload = (params) => {
  return request.post(`/school/user/teacher/recruit/upload`, params)
}