import { getSettled, settledUpload, applySettled } from '@/api/teacherRecruit'
import { getOptionsBatch } from '@/api/common'
import config from "@/config/common.config"
import reg from '@/config/reg.config'
import { storage } from 'ais-common-utils'
import CountrySelect from '@/components/Common/CountrySelect'
export default {
  components:{
    CountrySelect
  },
  data () {
    return {
      form:{
        status:0,
        countryCode:86
      },
      showPicker:false, //展开选择器
      activeFormConfig:{

      }, //选择器下拉
      pickerIndex:0, //选择器的值
      initData:false,
      config,
      optionsData:[]
    }
  },
  computed:{
    isEnglish() {
      //return this.$store.state.common.locale === 'en'
      return this.$i18n.locale === 'en'
    },
    columns() {
      return this.activeFormConfig.options || []
    },
    titleData() {
      return [
        this.$t('pages/teacherRecruit_3'),
        this.$t('pages/teacherRecruit_4'),
        this.$t('pages/teacherRecruit_5'),
      ]
    },
    formConfig() {
      const { domains, intention, userPosition, education } = this.optionsData
      return [
        { label:this.$t('pages/teacherRecruit_6'), prop:'name', type:'input', required:true },
        { label:this.$t('pages/teacherRecruit_7'), prop:'phone', type:'input', required:true },
        { label:this.$t('pages/teacherRecruit_8'), prop:'position', type:'select', options:userPosition, required:true },
        { label:this.$t('pages/teacherRecruit_9'), prop:'education', type:'select', options:education, required:true },
        { label:this.$t('pages/teacherRecruit_10'), prop:'domain', type:'select', options:domains, required:true},
        { label:this.$t('pages/teacherRecruit_11'), prop:'email', type:'input', required:false, formAttr: { type: "email" } },
        { label:this.$t('pages/teacherRecruit_12'), prop:'intention', type:'select', options:intention },
        { label:this.$t('pages/teacherRecruit_13'), prop:'resume', type:'upload', required:false }
      ]
    }
  },
  mounted(){
    //记住当前语言
    this.$i18n.locale = storage.get('teacherLang') ? 'en' : 'zh'
    this.$store.commit('common/setLocale', this.$i18n.locale)
    this.getHandleInfo()
  },
  beforeDestroy() {
    this.$i18n.locale === 'zh'
    this.$store.commit('common/setLocale', 'zh')
  },
  methods:{
    //语言切换
    langChange() {
      this.$i18n.locale = this.isEnglish ? 'zh' : 'en'
      this.$store.commit('common/setLocale', this.$i18n.locale)
      storage.set('teacherLang', this.isEnglish)
    },
    //获取入驻信息
    async getHandleInfo(){
      const { data } = await getSettled()
      if(data.code === 0){
        this.form = { 
          ...data.data.draft, 
          status:data.data.status || 0, 
          countryCode:data.data.draft && data.data.draft.countryCode ? data.data.draft.countryCode : 86,
          audit:data.data.audit || null, 
          intention:data.data.draft && data.data.draft.intention ? data.data.draft.intention : null 
        }
        if ([0, 4].includes(this.form.status)) {
          const { data:{code, data:optionsData} }= await getOptionsBatch('userPosition,domains,intention,education')
          if (code === 0) {
            this.optionsData = optionsData
          }
        }
      }
      this.initData = true
    },
    //头像上传接口
    async uploadFileHandle(file){
      let formData = new FormData()
      formData.append("file", file.file)
      const {data} = await settledUpload(formData)
      if (data.code === 0) {
        this.$toast.success("上传成功")
        this.$set(this.form, 'resume', data.data)
      }
    },
    async onSubmit() {
      const { email:regEmail} = reg
      const { email } = this.form
      //验证手机
      //if (!regPhone.test(phone)) return this.$toast.fail('请输入正确手机号码!')
      //验证邮箱
      if (email && !regEmail.test(email)) return this.$toast.fail('请输入正确邮箱地址!')
      if(!this.form.intention) {
        delete this.form.intention
      }
      const { data } = await applySettled(this.form)
      if (data.code === 0) {
        this.$toast.success(`提交成功!`)
        this.$set(this.form, 'status', 1)
      }
    },
    //选择器选中
    onConfirm(value) {
      this.$set(this.form, this.activeFormConfig.prop, value.value)
      this.showPicker = false
      this.pickerIndex = 0
    },
    //打开选中器
    pickerShow (item) {
      this.activeFormConfig = item
      this.pickerIndex = !this.form[item.prop] ? 0 : this.activeFormConfig.options.findIndex(p => p.value === this.form[item.prop])
      this.showPicker = true
    },
    changeFormValue(item) {
      const file = item.options.find(p=>p.value === this.form[item.prop])
      return file && file.name ? (this.isEnglish ? file.en||null : file.name ) : null
    },
    changeStatus() {
      this.$set(this.form, 'status', 0)
    }
  },
  head(){
    return {
      title:'艾思云课堂'
    }
  }
}
